.fade-in {
    animation: fadeIn 0.5s ease-in-out forwards; 
}

@keyframes fadeIn {
    from {
        opacity: 0; 
        transform: translateY(-10px); 
    }
    to {
        opacity: 1; 
        transform: translateY(0);
    }
}

ul li a:hover {
    border-radius: 8px; /* Ensure rounded corners */
}